:root {
  --aurora-primary: #6366f1;
  --aurora-primary-dark: #4f46e5;
  --aurora-primary-light: #818cf8;
  --aurora-secondary: #8b5cf6;
  --aurora-secondary-dark: #7c3aed;
  --aurora-secondary-light: #a78bfa;
  --aurora-accent: #06b6d4;
  --aurora-accent-dark: #0891b2;
  --aurora-accent-light: #22d3ee;
  --aurora-gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --aurora-gradient-accent: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  --aurora-gradient-hero: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #06b6d4 100%);
  --bg-primary: #fff;
  --bg-secondary: #f8fafc;
  --bg-tertiary: #f1f5f9;
  --text-primary: #0f172a;
  --text-secondary: #475569;
  --text-tertiary: #94a3b8;
  --border-color: #e2e8f0;
  --border-color-light: #f1f5f9;
  --success: #10b981;
  --success-light: #d1fae5;
  --warning: #f59e0b;
  --warning-light: #fef3c7;
  --error: #ef4444;
  --error-light: #fee2e2;
  --info: #3b82f6;
  --info-light: #dbeafe;
  --shadow-sm: 0 1px 2px #0000000d;
  --shadow-md: 0 4px 6px #00000012;
  --shadow-lg: 0 10px 15px #0000001a;
  --shadow-xl: 0 20px 25px #00000026;
  --shadow-2xl: 0 25px 50px #00000040;
  --glass-bg: #ffffffbf;
  --glass-border: #ffffff4d;
