  --transition-fast: .15s cubic-bezier(.4, 0, .2, 1);
  --transition-base: .3s cubic-bezier(.4, 0, .2, 1);
  --transition-slow: .5s cubic-bezier(.4, 0, .2, 1);
}

[data-theme="dark"] {
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-tertiary: #334155;
  --text-primary: #f1f5f9;
  --text-secondary: #cbd5e1;
  --text-tertiary: #64748b;
  --border-color: #334155;
  --border-color-light: #1e293b;
  --glass-bg: #0f172abf;
  --glass-border: #ffffff1a;
  --glass-shadow: 0 8px 32px #00000080;
}

.navbar {
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-bottom: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  padding: var(--space-md) 0;
  transition: all var(--transition-base);
  background: var(--glass-bg) !important;
}

.navbar-brand {
  background: var(--aurora-gradient-primary);
  -webkit-text-fill-color: transparent;
  align-items: center;
  gap: var(--space-sm);
  transition: all var(--transition-base);
  background-clip: text;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
}

.navbar-brand:hover {
  transform: scale(1.05);
}

.navbar-brand i {
  background: var(--aurora-gradient-accent);
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: 2s ease-in-out infinite pulse;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: .8;
    transform: scale(1.1);
  }
}

.nav-link {
  transition: all var(--transition-fast);
  font-weight: 500;
  position: relative;
  color: var(--text-secondary) !important;
  padding: var(--space-sm) var(--space-md) !important;
}

.nav-link:hover {
  transform: translateY(-2px);
  color: var(--aurora-primary) !important;
}

.nav-link:after {
  content: "";
  background: var(--aurora-gradient-accent);
  width: 0;
  height: 2px;
  transition: all var(--transition-base);
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.nav-link:hover:after {
  width: 80%;
}

.navbar a[href*="github"] {
  border-radius: var(--radius-full);
  width: 40px;
  height: 40px;
  transition: all var(--transition-base);
  justify-content: center;
  align-items: center;
  display: inline-flex;
}

.navbar a[href*="github"] i {
  color: var(--text-primary);
  transition: all var(--transition-base);
  font-size: 1.5rem;
}

.navbar a[href*="github"]:hover {
  background: var(--bg-secondary);
  transform: scale(1.1);
}

.navbar a[href*="github"]:hover i {
  color: var(--aurora-primary);
  transform: rotate(360deg);
}

