  overflow: hidden;
}

.btn:before {
  content: "";
  background: #ffffff4d;
  border-radius: 50%;
  width: 0;
  height: 0;
  transition: width .6s, height .6s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn:hover:before {
  width: 300px;
